.movie-back-drop {
  background-size: cover;
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-height: 100vh;
  padding: 0;
}

.movie-details-container {
  background:rgb(99, 99, 99, 0.85);
  font-weight: 500;
  color: white;
  margin: 2rem 2rem 2rem 2rem;
  padding: 1rem 2rem 1rem 2rem;
  max-width: 50rem;
  border-radius: 1rem;
}

.title {
  font-size: 4rem;
  margin-bottom: 0.8rem;
}

.release-date {
  font-size: 1.25rem;
  margin-top: 0.8rem;
}

.tagline {
  font-size: 1.7rem;
  font-style: italic;
}

.overview {
  font-size: 1.5rem;
}

.return-home-btn {
  background: white;
  color: rgb(99, 99, 99, 0.9);
  font-size: 2rem;
  border-radius: 10rem;
  border: 0;
  padding: 1rem;
  margin: 1rem;
  box-shadow: 0 0.25rem 0.5rem 0 black;
  cursor: pointer;
}

.return-home-btn:hover {
  transform: scale(1.10);
  box-shadow: 0 0.5rem 0.75rem 0 #880808;
}